import { property } from 'lit/decorators.js';
import { watch } from '../../internal/watch';
import componentStyles from '../../styles/component.styles';
import styles from './divider.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';

/**
 * @summary Dividers are used to visually separate or group elements.
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net/?path=/docs/components-divider--docs
 * @status stable
 * @since 2.0
 *
 * @cssproperty --color - The color of the divider.
 * @cssproperty --width - The width of the divider.
 * @cssproperty --spacing - The spacing of the divider.
 */
export default class Divider extends UIKitElement {
  static styles: CSSResultGroup = [componentStyles, styles];

  /** The divider's theme variant. */
  @property({ reflect: true }) variant: 'primary' | 'secondary' = 'primary';

  /** Draws the divider in a vertical orientation. */
  @property({ type: Boolean, reflect: true }) vertical = false;

  /** The width of the divider. */
  @property({ type: Number, reflect: true }) width = 0;

  connectedCallback() {
    super.connectedCallback();
    this.setAttribute('role', 'separator');
  }

  @watch('vertical')
  handleVerticalChange() {
    this.setAttribute('aria-orientation', this.vertical ? 'vertical' : 'horizontal');
  }

  @watch('variant')
  handleVariantChange() {
    const widthStyle = `--width : ${this.width > 0 ? this.width : 1}px`;
    this.setAttribute(
      'style',
      this.variant === 'primary'
        ? `--color:var(--koerber-color-border); ${widthStyle}`
        : `--color:var(--koerber-color-border-secondary); ${widthStyle}`
    );
  }

  @watch('width')
  handleWidthChange() {
    const colorStyle =
      this.variant === 'primary'
        ? '--color:var(--koerber-color-border); '
        : '--color:var(--koerber-color-border-secondary); ';
    this.setAttribute('style', colorStyle + `--width: ${this.width > 0 ? this.width : 1}px`);
  }
}
